.rbc-btn-group {
    display: none;
    white-space: nowrap;
}

.card-calender{background: #f9f9f9; padding: 20px; border-radius: 20px;}

.rbc-date-cell {
    flex: 1 1;
    min-width: 0;
    padding: 8px;
    text-align: right;
}

.rbc-header {
    overflow: hidden;
    flex: 1 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding: 8px;
    text-align: center;
    vertical-align: middle;
    font-weight: bold;
    font-size: 90%;
    min-height: 0;
    border-bottom: 1px solid #DDD;
}

.rbc-calendar {
    box-sizing: border-box;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: stretch;
}

.rbc-toolbar {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin-bottom: 0px;
    font-size: 16px;
}

.rbc-toolbar .rbc-toolbar-label{    
    border-radius: 8px 8px 0px 0px;
    background: #266e7b;
    padding: 8px;
    color: #fff;
}

.rbc-month-view{    
    position: relative;
    border: 1px solid #DDD;
    display: flex;
    flex-direction: column;
    flex: 1 0;
    width: 100%;
    user-select: none;
    -webkit-user-select: none;
    height: 100%;
    border-radius: 0px 0px 8px 8px;
    overflow: hidden;
}


.MissedDates{
    margin-top: 40px;
    border-top: 1px solid #e6e6e6;
    padding-top: 30px;
}

.previous-history{
